<script>
	import Modal from './components/Modal.svelte'
	import Search from './components/Search.svelte'
	import MediaItem from './components/MediaItem.svelte'
	let items = []
	let page = 2
	let query_val = ""
	let year_val = ""
	let scroll_old = 0
	let scroll_new = 0
	let single = {
		Title: "Guardians of the Galaxy Vol. 2",
		Year: "2017",
		Rated: "PG-13",
		Released: "05 May 2017",
		Runtime: "136 min",
		Genre: "Action, Adventure, Comedy, Sci-Fi",
		Director: "James Gunn",
		Writer: "James Gunn, Dan Abnett (based on the Marvel comics by), Andy Lanning (based on the Marvel comics by), Steve Englehart (Star-Lord created by), Steve Gan (Star-Lord created by), Jim Starlin (Gamora and Drax created by), Stan Lee (Groot created by), Larry Lieber (Groot created by), Jack Kirby (Groot created by), Bill Mantlo (Rocket Raccoon created by), Keith Giffen (Rocket Raccoon created by), Steve Gerber (Howard the Duck created by), Val Mayerik (Howard the Duck created by)",
		Actors: "Chris Pratt, Zoe Saldana, Dave Bautista, Vin Diesel",
		Plot: "The Guardians struggle to keep together as a team while dealing with their personal family issues, notably Star-Lord's encounter with his father the ambitious celestial being Ego.",
		Language: "English",
		Country: "USA",
		Awards: "Nominated for 1 Oscar. Another 15 wins & 56 nominations.",
		Poster: "https://m.media-amazon.com/images/M/MV5BNjM0NTc0NzItM2FlYS00YzEwLWE0YmUtNTA2ZWIzODc2OTgxXkEyXkFqcGdeQXVyNTgwNzIyNzg@._V1_SX300.jpg",
		Ratings: [
			{
				Source: "Internet Movie Database",
				Value: "7.6/10"
			},
			{
				Source: "Rotten Tomatoes",
				Value: "85%"
			},
			{
				Source: "Metacritic",
				Value: "67/100"
			}
		],
		Metascore: "67",
		imdbRating: "7.6",
		imdbVotes: "545,424",
		imdbID: "tt3896198",
		Type: "movie",
		DVD: "22 Aug 2017",
		BoxOffice: "$389,804,217",
		Production: "Walt Disney Pictures",
		Website: "N/A",
		Response: "True"
	}
	const handleSearch = async () => {
		let next = document.getElementById("next")
		let intro = document.getElementById("intro-wrapper")
		let query = document.getElementById("search-text").value
		query_val = query !== "" ? `&s=${query}` : ""
		let year = document.getElementById("search-year").value
		year_val = year !== "" ? `&y=${year}` : ""
		let none = document.getElementById("none")
		let response = await fetch(`http://www.omdbapi.com/?apikey=${"7aeaf5bd"}${query_val}${year_val}`)
		let jason = await response.json()
		if (jason.Response == "True") {
			items = []
			page = 2
			intro.classList.add("hide")
			none.classList.add("hide")
			next.classList.remove("hide")
			items = jason.Search
		} else {
			intro.classList.add("hide")
			none.classList.remove("hide")
		}
	}
	const load = async () => {
		let next = document.getElementById("next")
		let query = document.getElementById("search-text").value
		let response = await fetch(`http://www.omdbapi.com/?apikey=${"7aeaf5bd"}${query_val}${year_val}&page=${page}`)
		let jason = await response.json()
		if (jason.Response == "True") {
			next.classList.remove("hide")
			end.classList.add("hide")
			items = items.concat(jason.Search)
			page++
		} else {
			let end = document.getElementById("end")
			end.classList.remove("hide")
		}
	}
	const modal = async (e) => {
		let modal = document.getElementById("modal-wrapper")
		let response = await fetch(`http://www.omdbapi.com/?apikey=${"7aeaf5bd"}&i=${e.detail.id}`)
		let jason = await response.json()
		single = jason
		modal.classList.remove("hide")
	}
	const handleScroll = () => {
		let y = window.scrollY
		scroll_new = y
		let search_wrapper = document.getElementById("search")
		if (scroll_new > scroll_old) {
			search_wrapper.style.top = "-5rem"
			scroll_old = y
		} else if (scroll_new < scroll_old) {
			search_wrapper.style.top = "0rem"
			scroll_old = y
		}
	}
</script>

<style lang="sass">#wrapper {
  margin: 0 auto;
  padding: 0.5rem;
  margin-top: 4rem;
  max-width: 80rem;
  min-height: 100vh; }
  #wrapper #next {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    width: 10rem;
    padding: 0.5rem;
    cursor: pointer;
    background: #eee;
    margin: 2rem auto;
    font-size: 1.5rem;
    line-height: 1.5rem;
    border-radius: 0.25rem;
    transition: all 0.25s ease-in-out;
    box-shadow: 0 0 0.25rem 0.1rem rgba(0, 0, 0, 0.25); }
    #wrapper #next:hover {
      box-shadow: 0 0 0.5rem 0.1rem rgba(0, 0, 0, 0.33); }
  #wrapper #media-items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    align-items: stretch; }
  #wrapper #intro-wrapper {
    margin: 0 auto;
    max-width: 33rem;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    background: rgba(255, 255, 255, 0.25);
    border-radius: 0.5rem; }
    #wrapper #intro-wrapper .intro-wrapper-text {
      font-size: 2rem;
      line-height: 2rem;
      padding: 2rem;
      text-align: center;
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: center; }</style>

<svelte:window on:scroll={handleScroll}/>

<svelte:head>
	<title>Open Movie Database (OMDb)</title>
	<meta name="description" content="Browse media in the library of the Open Movie Database">
</svelte:head>

<div id="wrapper">
	<div id="intro-wrapper">
		<p class="intro-wrapper-text">Use the search box to browse the media in the library of the Internet Movie Database (IMDb)</p>
	</div>
	<div id="media-items">
		<Search on:search={handleSearch} />
		{#each items as item, index}
			<MediaItem on:showModal={modal} info={item} />
		{/each}
	</div>
	<p id="next" class="hide" on:click={load}>{`Page ${page}`}</p>
	<p id="end" class="hide">End of Results</p>
	<p id="none" class="hide">No results found. Try another search.</p>
	<Modal info={single} />
</div>
