<script>
	const handleClick = (e) => {
		let modal = document.getElementById("modal-wrapper")
		if (e.target.id == "modal-wrapper") {
			modal.classList.add("hide")
		}
	}
	export let info = {}
	$: plot = info.Plot ? info.Plot : "N/A"
	$: title = info.Title ? info.Title : "N/A"
	$: rated = info.Rated ? info.Rated : "N/A"
	$: genre = info.Genre ? info.Genre : "N/A"
	$: awards = info.Awards ? info.Awards : "N/A"
	$: imdbid = info.imdbID ? info.imdbID : "N/A"
	$: ratings = info.Ratings ? info.Ratings : "N/A"
	$: language = info.Language ? info.Language : "N/A"
	$: year = info.Year ? parseInt(info.Year) : "N/A"
	$: actors = info.Actors ? `Starring: ${info.Actors}` : "N/A"
	$: writer = info.Writer ? `Writer(s): ${info.Writer}` : "N/A"
	$: metascore = info.Metascore ? parseFloat(info.Metascore) : "N/A"
	$: director = info.Director ? `Director: ${info.Director}` : "N/A"
	$: released = info.Released ? `Released: ${info.Released}` : "N/A"
	$: poster = info.Poster !== "N/A" ? info.Poster : "/img/placeholder.jpg"
	$: production = info.Production ? `Production: ${info.Production}` : " Production: N/A"
	$: full_title = info.Title ? `${info.Title} (${info.Year})` : "N/A"
	$: imdblink = info.imdbID ? `https://www.imdb.com/title/${info.imdbID}/` : "N/A"
	$: imdbvotes = {
			formatted: info.imdbVotes ? info.imdbVotes : "N/A",
			unformatted: info.imdbVotes ? parseFloat(info.imdbVotes.replace(/[^0-9]/gi, "")) : "N/A"
	}
	$: boxoffice = {
			formatted: info.BoxOffice ? info.BoxOffice : "N/A",
			unformatted: info.BoxOffice ? parseFloat(info.BoxOffice.replace(/[^0-9.]/gi, "")) : "N/A"
	}
	$: imdbrating = {
			formatted: info.imdbRating ? info.imdbRating : "N/A",
			unformatted: info.imdbRating ? parseFloat(info.imdbRating.replace(/[^0-9.]/gi, "")) : "N/A"
	}
	$: runtime = {
			formatted: info.Runtime ? info.Runtime : "N/A",
			unformatted: info.Runtime ? parseFloat(info.Runtime.replace(/[^0-9]/gi, "")) : "N/A"
	}
</script>

<style lang="sass">#modal-wrapper {
  height: 100vh;
  min-width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  overflow-y: scroll;
  -webkit-animation: fadeIn 0.25s ease-in-out;
          animation: fadeIn 0.25s ease-in-out;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.75); }
  #modal-wrapper #modal {
    color: white;
    margin: 1rem;
    width: 100%;
    background: #eee;
    max-width: 90rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    border-radius: 0.5rem;
    transition: box-shadow 0.25s ease-in-out;
    box-shadow: 0 0 0.5rem 0.15rem rgba(0, 0, 0, 0.15); }
    #modal-wrapper #modal:hover {
      box-shadow: 0 0 0.5rem 0.33rem rgba(0, 0, 0, 0.15); }
    #modal-wrapper #modal .modal-poster-wrapper {
      min-width: 100%;
      margin: 1.5rem;
      min-height: 445px; }
      @media (min-width: 45rem) {
        #modal-wrapper #modal .modal-poster-wrapper {
          min-width: auto; } }
      #modal-wrapper #modal .modal-poster-wrapper .modal-poster {
        height: 445px;
        margin: 0 auto;
        max-width: 100%;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        box-sizing: content-box;
        border-radius: 0.25rem;
        box-shadow: 0 0 0.5rem 0.15rem rgba(0, 0, 0, 0.15); }
    #modal-wrapper #modal .modal-info-wrapper {
      flex: 1;
      display: flex;
      flex-wrap: wrap;
      flex-direction: column; }
      #modal-wrapper #modal .modal-info-wrapper .modal-info {
        height: auto;
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center; }
        #modal-wrapper #modal .modal-info-wrapper .modal-info.plot {
          flex: 1;
          margin: 0 auto;
          max-width: 50rem;
          padding: 2rem 0.5rem;
          transition: all 0.25s ease-in-out; }
          #modal-wrapper #modal .modal-info-wrapper .modal-info.plot .modal-info-item {
            text-indent: 0.5rem;
            font-size: 1.1rem;
            line-height: 1.1rem; }
            @media (min-width: 56rem) {
              #modal-wrapper #modal .modal-info-wrapper .modal-info.plot .modal-info-item {
                font-size: 1.5rem;
                line-height: 1.5rem; } }
        #modal-wrapper #modal .modal-info-wrapper .modal-info .modal-info-item {
          padding: 0.25rem;
          display: flex;
          flex-wrap: wrap;
          align-items: center;
          justify-content: center; }
          #modal-wrapper #modal .modal-info-wrapper .modal-info .modal-info-item.title {
            text-align: center;
            margin-bottom: 1.5rem;
            font-size: 1.25rem;
            line-height: 1.25rem; }
            @media (min-width: 56rem) {
              #modal-wrapper #modal .modal-info-wrapper .modal-info .modal-info-item.title {
                font-size: 2rem;
                line-height: 2rem; } }
          #modal-wrapper #modal .modal-info-wrapper .modal-info .modal-info-item.short {
            margin: 0 0.5rem;
            font-size: 0.9rem;
            transition: all 0.25s ease-in-out; }
            @media (min-width: 56rem) {
              #modal-wrapper #modal .modal-info-wrapper .modal-info .modal-info-item.short {
                font-size: 1.1rem; } }
          #modal-wrapper #modal .modal-info-wrapper .modal-info .modal-info-item.rating-wrapper {
            flex: 1;
            display: block;
            padding: 0;
            width: 100%;
            height: 1.5rem;
            border-radius: 0.5rem;
            overflow: hidden;
            background: #ab5454; }
            #modal-wrapper #modal .modal-info-wrapper .modal-info .modal-info-item.rating-wrapper .rating-fill {
              height: 100%;
              background: #2a562a; }
          #modal-wrapper #modal .modal-info-wrapper .modal-info .modal-info-item.icon-wrapper .icon {
            width: 3rem;
            height: 3rem;
            box-sizing: content-box; }
          #modal-wrapper #modal .modal-info-wrapper .modal-info .modal-info-item.text-wrapper .text {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            justify-content: center;
            font-size: 1.25rem;
            margin: 0 0.25rem; }
            #modal-wrapper #modal .modal-info-wrapper .modal-info .modal-info-item.text-wrapper .text.number {
              opacity: 0.5; }</style>

<div id="modal-wrapper" class="hide" on:click={handleClick}>
	<div id="modal">
		<div class="modal-poster-wrapper">
			<img class="modal-poster" src={poster} alt="poster">
		</div>
		<div class="modal-info-wrapper">
			<div class="modal-info">
				<div class="modal-info-item title">{full_title}</div>
			</div>
			<div class="modal-info">
				<div class="modal-info-item short">{language}</div>
				<div class="modal-info-item short">{rated}</div>
				<div class="modal-info-item short">{runtime.formatted}</div>
				<div class="modal-info-item short">{genre}</div>
			</div>
			<div class="modal-info">
				<div class="modal-info-item short">{actors}</div>
			</div>
			<div class="modal-info plot">
				<div class="modal-info-item">{plot}</div>
			</div>
			<div class="modal-info">
				<div class="modal-info-item short">{released}</div>
				<div class="modal-info-item short">{`Box Office: ${boxoffice.formatted}`}</div>
				<div class="modal-info-item short">{director}</div>
				<div class="modal-info-item short">{production}</div>
				<div class="modal-info-item short">{writer}</div>
			</div>
			<div class="modal-info">
				<div class="modal-info-item icon-wrapper">
					<img class="icon" src="/icons/imdb.svg" alt="imdb">
				</div>
				<div class="modal-info-item rating-wrapper">
					<div class="rating-fill" style={`width: ${imdbrating.unformatted * 10}%;`}></div>
				</div>
				<div class="modal-info-item text-wrapper">
					<span class="text">{imdbrating.formatted}</span>
					<span class="text number">{`(${imdbvotes.formatted})`}</span>
				</div>
			</div>
		</div>
	</div>
</div>
